import PropTypes from 'prop-types';
import { styled } from 'styled-components';

const testStr = 'The quick brown fox jumps over the lazy dog';

const Wrapper = styled.section`
  margin-top: 3rem;

  &:first-child {
    margin-top: 0;
  }
`;

const Title = styled.h1`
  margin: 0 0 20px;
`;

const SubTitle = styled.h2`
  margin: 0 0 20px;
  font-size: 20px;
`;

const TestLabel = styled.span`
  position: absolute;
  top: -45px;
  right: 0;
  color: ${({ theme }) => theme.color.ink.base};
  font-size: 18px;
`;

const TestStringComponent = ({ className, label, children, style }) => (
  <p className={className}>
    <TestLabel>{label}</TestLabel>
    <span style={style}>{children}</span>
  </p>
);

TestStringComponent.propTypes = {
  className: PropTypes.string,
  label: PropTypes.string,
  children: PropTypes.node,
  style: PropTypes.object,
};

const TestString = styled(TestStringComponent)`
  position: relative;
  padding: 20px;
  border-radius: 5px;
  background: ${({ theme }) => theme.color.canvas.base};
  font-size: 60px;
  line-height: 60px;
`;

const ComparisonControlString = styled(TestString)`
  color: ${({ theme }) => theme.color.semiotic.success};
`;

const ComparisonTestString = styled(TestString)`
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  background: transparent;
  color: ${({ theme }) => theme.color.primary.base};
  opacity: 0.7;
`;

const ComparisonContainer = styled.div`
  position: relative;
`;

/** @todo create "copy to clipboard" components for these values */

const Label = styled.dd`
  font: 18px/26px ${(props) => props.theme.fontFamily.secondaryFont};
`;

const Name = ({ text }) => <Label>{text}</Label>;

const FontFace = ({ title, variants, caps }) => {
  return (
    <Wrapper>
      <Title>{title}</Title>
      {variants.map(({ weight, style, src, fallback }) => {
        const isBold = weight === 'bold';
        const isItalic = style === 'italic';

        return (
          <div key={src}>
            <SubTitle>
              {isBold && isItalic
                ? 'bold italic'
                : isBold
                  ? 'bold'
                  : isItalic
                    ? 'italic'
                    : 'default'}
              - {src}
            </SubTitle>
            <TestString
              label="original"
              style={{
                fontFamily: title,
                fontWeight: weight,
                fontStyle: style,
              }}
            >
              {caps ? testStr.toUpperCase() : testStr}
            </TestString>
            {fallback && (
              <>
                <TestString
                  label="fallback"
                  style={{
                    fontFamily: `${title} Fallback`,
                    fontWeight: weight,
                    fontStyle: style,
                  }}
                >
                  {caps ? testStr.toUpperCase() : testStr}
                </TestString>
                <ComparisonContainer>
                  <ComparisonControlString
                    label="comparison"
                    style={{
                      fontFamily: title,
                      fontWeight: weight,
                      fontStyle: style,
                    }}
                  >
                    {caps ? testStr.toUpperCase() : testStr}
                  </ComparisonControlString>
                  <ComparisonTestString
                    style={{
                      fontFamily: `${title} Fallback`,
                      fontWeight: weight,
                      fontStyle: style,
                    }}
                  >
                    {caps ? testStr.toUpperCase() : testStr}
                  </ComparisonTestString>
                </ComparisonContainer>
              </>
            )}
          </div>
        );
      })}
    </Wrapper>
  );
};

Name.propTypes = {
  text: PropTypes.string.isRequired,
};

FontFace.propTypes = {
  title: PropTypes.string.isRequired,
  variants: PropTypes.array.isRequired,
  caps: PropTypes.bool,
};

export default FontFace;
